.second-view-container {
  height: 700px;
  background: $clr1 url("../images/Group\ 8.png") no-repeat calc(50% + 728px) 100%;

  .container {
    padding-top: 140px;
  }

  .second-view-title:before {
    content: " ";
    display: block;
    background-color: $clr3;
    height: 4px;
    width: 50px;
    margin-bottom: 54px;
  }

  h1 {
    font-size: rem(48px);
    line-height: rem(48px);
    width: 445px;
  }

  .features-container {
    padding-top: 58px;
    height: 346px;

    .feature {
      width: 445px;
      position: relative;
      margin-bottom: 20px;

      h1 {
        font-size: rem(18px);
        line-height: rem(28px);
        color: $clr3;
      }

      p {
        font-size: 15px;
      }

      &.first::before {
        content: url("../images/Group\ 11.svg");
        display: block;
        position: absolute;
        left: -98px;
      }

      &.second::before {
        content: url("../images/Group\ 12.svg");
        display: block;
        position: absolute;
        left: -98px;
      }

      &.third::before {
        content: url("../images/Group\ 13.svg");
        display: block;
        position: absolute;
        left: -98px;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .second-view-container {
    background: $clr1;
  }
}

@media screen and (max-width: 768px) {
  .second-view-container {
    background: $clr1 url("../images/bg-pattern-home-3.svg") no-repeat calc(50% + 384px) 0%;
    display: flex;
    justify-content: center;

    .container {
      flex-direction: column;
      justify-content: unset;
      padding-top: 0;
    }

    .second-view-title:before {
      margin-top: 98px;
      margin-bottom: 32px;
    }

    h1 {
      font-size: rem(32px);
      line-height: rem(32px);
    }

    .features-container {
      padding-top: 38px;
      padding-left: 92px;

      .feature {
        width: 478px;

        &.first::before {
          content: url("../images/Group\ 11\ v2.png");
        }

        &.second::before {
          content: url("../images/Group\ 12\ v2.png");
        }

        &.third::before {
          content: url("../images/Group\ 13\ v2.png");
        }
      }
    }
  }
}

@media screen and (max-width: $mobile-breakpoint) {
  .second-view-container {
    height: 1073px;
    background-position: calc(50% + 230px) 0%;

    .second-view-title:before {
      margin-top: 58px;
    }

    .second-view-title {
      width: 240px;
    }

    .features-container {
      text-align: center;
      padding-left: 0;

      .feature {
        width: 327px;
        margin-top: 90px;

        h1 {
          width: 327px;
        }

        &.first::before {
          top: -80px;
          left: 130px;
        }

        &.second::before {
          top: -80px;
          left: 130px;
        }

        &.third::before {
          top: -80px;
          left: 130px;
        }
      }
    }
  }
}
